h1, h2, h3 {
  margin: 0;
  font-family: Montserrat, sans-serif;
  font-weight: 400;
}

p {
  margin: 0;
}

.page {
  box-sizing: border-box;
  scroll-behavior: smooth;
  min-width: 320px;
  margin: auto;
  font-family: IBM Plex Sans, sans-serif;
  font-size: 16px;
  font-weight: 500;
  line-height: 100%;
  position: relative;
}

.page__body {
  margin: 0;
  padding: 0;
}

.page__body--overflow-hidden {
  position: relative;
  overflow: hidden;
}

.page__menu {
  z-index: 10;
  opacity: 0;
  pointer-events: none;
  background-color: #1a5a4c;
  transition: all .3s;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  transform: translateX(-100%);
}

.page__menu--target {
  opacity: 1;
  pointer-events: all;
  overflow: hidden;
  transform: translateX(0);
}

.page__wrapper {
  z-index: 5;
  opacity: .5;
  background: #18181a;
  display: none;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.page__wrapper--on {
  display: block;
}

.page__icon {
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  grid-column: 1 / 2;
  width: 24px;
  height: 24px;
  margin-top: 27px;
  position: absolute;
}

@media (min-width: 768px) {
  .page__icon {
    grid-column: 1 / 2;
    margin-top: 40px;
  }
}

.page__title {
  color: #0f0e08;
  text-align: center;
  margin-bottom: 40px;
  font-family: Montserrat, sans-serif;
  font-size: 36px;
  font-weight: 700;
  line-height: 100%;
}

@media (min-width: 768px) {
  .page__title {
    text-align: left;
    margin-bottom: 48px;
    font-size: 48px;
  }
}

@media (min-width: 1280px) {
  .page__title {
    margin-bottom: 56px;
  }
}

.page__main {
  margin-bottom: 80px;
}

@media (min-width: 768px) {
  .page__main {
    margin-bottom: 120px;
  }
}

@media (min-width: 1280px) {
  .page__main {
    margin-bottom: 87px;
  }
}

.swiper {
  --swiper-pagination-top: 425px;
  height: 440px;
  padding-left: 20px !important;
}

@media (min-width: 768px) {
  .swiper {
    --swiper-pagination-top: 569px;
    height: 583px;
    padding-left: 34px !important;
  }
}

@media (min-width: 1280px) {
  .swiper {
    grid-column: 1 / -1;
    height: 990px;
    padding-left: 0 !important;
    padding-right: 0 !important;
  }
}

.swiper-wrapper {
  width: auto;
}

@media (min-width: 1280px) {
  .swiper-wrapper {
    grid-template-columns: repeat(12, 1fr);
    gap: 30px;
    display: grid !important;
  }

  .swiper-slide-col-4 {
    grid-column: span 4;
  }

  .swiper-slide-col-8 {
    grid-column: span 8;
  }

  .swiper-slide-row-1 {
    grid-row: 1 / 2;
  }

  .swiper-slide-row-2 {
    grid-row: 2 / 3;
  }
}

.swiper-imagewrap {
  height: 410px;
  transition: scale .3s;
  overflow: hidden;
}

.swiper-imagewrap:hover {
  scale: 1.02;
}

@media (min-width: 768px) {
  .swiper-imagewrap {
    height: 543px;
  }
}

@media (min-width: 1280px) {
  .swiper-imagewrap-row-1 {
    height: 600px;
  }

  .swiper-imagewrap-row-2 {
    grid-row: 2 / 3;
    height: 360px;
  }
}

.swiper-image {
  object-fit: cover;
  width: 100%;
  height: 100%;
}

.swiper-pagination {
  transform: translateY(34px);
}

.swiper-pagination-bullet {
  --swiper-pagination-bullet-width: 9px;
  --swiper-pagination-bullet-height: 9px;
  --swiper-pagination-bullet-horizontal-gap: 11px;
}

.swiper-pagination-bullet-active {
